<!-- 修改密保答案 -->
<template>
  <div class="user_info">
    <div class="row">

      <div class="group">
        <div class="title">1、请先回答您设置安全问题答案</div>

        <el-form :model="old" label-position='right' label-width="100px" class="form">
          <el-form-item label="安全问题：">
            <span>{{ old.question }}</span>
          </el-form-item>

          <el-form-item label="问题答案：">
            <el-input class="answer" v-model="old.answer" auto-complete="off" maxLength=6 minLength=6></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div class="group">
        <div class="title">2、请从新设置您的密保问题和答案</div>

        <el-form :model="newData" label-position='right' label-width="100px" class="form">
          <el-form-item label="安全问题：">
            <select v-model="newData.question">
              <option v-for="(item, index) in questions" :value="item">{{ item }}</option>
            </select>
          </el-form-item>

          <el-form-item label="问题答案：">
            <el-input class="answer" v-model="newData.answer" auto-complete="off" maxLength=6 minLength=6></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-button class="submit" type="primary" @click="submit()">确定修改</el-button>
          </el-form-item>

        </el-form>
      </div>

    </div>
  </div>
</template>

<script>

  export default {
    name: 'question',
    components: {

    },
    data: function () {
      return {
        old: {
          question: '',
          answer: '',
        },
        newData: {
          question: 'aaa',
          answer: '',
        },
        questions: [],
      }
    },
    created () {
      gapi.getUserQuestion()
        .then((res) => {
          if (res.code === 1) {
            this.old.question = res.data
          }
        })
        .catch((error) => {
          return false
        })
      gapi.getUserSecurityQuestions()
        .then((res) => {
          if (res.data.code === 1) {
            this.questions = res.data.data
            this.newData.question = this.questions.length > 0 ? this.questions[0] : ''
          }
        })
        .catch((error) => {
          return false
        })
    },
    methods: {
      submit: function() {
        let params = {
          question: this.old.question,
          oldAnswer: this.old.answer,
          newQuestion: this.newData.question,
          newAnswer: this.newData.answer,
        }
        params = this.$qs.stringify(params)
        gapi.changeSecurity(params).then(res => {
          if (res.code === 1) {
            this.$alert('成功设置新密保!', '', {
              confirmButtonText: '确定',
              center: true,
              callback: action => {
              }
            })
          } else {
              this.$alert(res.failure, '错误提示', {
                confirmButtonText: '',
                center: true,
                type: 'error'
              })
          }
        }).catch(err => {
          this.$alert(err, '错误提示', {
            confirmButtonText: '',
            center: true,
            type: 'error'
          })
        })
      }
    },
  }
</script>

<style scoped>
  .user_info {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    padding: 40px 30px 40px;
  }

  .row {
    display: flex;
    flex-direction: row;
  }

  .group {
    width: 300px;
    margin: 0px 20px 0px 0px;
    flex: 1;
    min-width: 200px;
  }

  .form {
    margin-top: 20px;
  }

  .answer {
    width: 250px;
    /*vertical-align: top;*/
  }

  .submit {
    margin-top: 20px;
  }

</style>



